
.poly{
    position: absolute;
    top: 50%;
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
    width: 0;
    height: 0;
    border-right: 100px solid rgb(203, 212, 114);
    border-top: 50px solid transparent;
    border-bottom: 50px solid transparent;
    opacity: 90%;
}

.poly2{
    position: absolute;
    right: 0px;
    margin: 0;
    top: 50%;
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
    width: 0;
    height: 0;
    border-left: 100px solid rgb(203, 212, 114);
    border-top: 50px solid transparent;
    border-bottom: 50px solid transparent;
    opacity: 90%;
}

div.cn {
    position: relative;
    width: 500px;
    height: 100%;
    background: gray;
}

div.inner {
    position: absolute;
    top: 50%;
    transform: translate(-50%,-50%);
    background: gold;
    padding: 2rem;
}


.arrow {
    border: solid black;
    border-width: 0 10px 10px 0;
    display: inline-block;
    padding: 10px;
    
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
  }
  